import { adapters } from "@rp/adapters"
import {
  ErrorInterceptor,
  RequestInterceptor,
  ResponseInterceptor,
} from "@rp/react/axios-api/types"

export const requestInterceptor: RequestInterceptor = async config => {
  const token = await adapters.api.getClerkToken()
  return {
    ...config,
    headers: {
      ...config.headers,
      ...(config.withCredentials ? { Authorization: `Bearer ${token}` } : {}),
    },
  }
}

export const requestErrorInterceptor: ErrorInterceptor = error => {
  return Promise.reject(error)
}

export const responseInterceptor: ResponseInterceptor = response => {
  return response
}

export const responseErrorInterceptor: ErrorInterceptor = error => {
  return Promise.reject(error)
}
